<template>
  <Modal ref="modal">
    <template slot="header" :bind="title">
      {{ title }}
    </template>
    <template slot="content">
      {{ content }}
    </template>
    <template slot="footer">
      <button @click="cancel">{{ cancelLabel }}</button>
      <button @click="ok">{{ okLabel }}</button>
    </template>
  </Modal>
</template>

<script>
import Modal from "./Modal.vue";
export default {
  props: ["title", "content", "okLabel", "cancelLabel"],
  components: { Modal },
  methods: {
    show() {
      this.$refs.modal.show();
    },
    hide() {
      this.$refs.modal.hide();
    },
    ok() {
      this.$emit("ok", "something");
      this.hide();
    },
    cancel() {
      this.hide();
    }
  }
};
</script>
