<template>
  <Modal ref="modal">
    <template slot="header">
      {{ title }}
    </template>
    <template slot="content">
      {{ content }}
    </template>
    <template slot="footer">
      <button @click="hide">{{ hideLabel }}</button>
    </template>
  </Modal>
</template>

<script>
import Modal from "./Modal.vue";
export default {
  props: ["title", "content", "hideLabel"],
  components: { Modal },
  methods: {
    show() {
      this.$refs.modal.show();
    },
    hide() {
      this.$refs.modal.hide();
    }
  }
};
</script>